import { useEffect, useState, useRef } from 'react';
import { useBlockNumber, useEthers } from '@usedapp/core';
import { useQuery } from '@apollo/client';
import { GET_NFT_BY_USER } from '../queries';


const useOwnedNFT = (NFT_COLLECTION, NFT_DESCRIPTION) => {

    const mountedRef = useRef(true);

    const [nft, setNft] = useState(undefined);

    const { account } = useEthers();

    const [user, setUser] = useState(undefined);

    useEffect(() => {
        if (account) {
          setUser(account.toLowerCase());
        } else {
          setUser(undefined);
        }
      }, [account]);

    const { data } = useQuery(GET_NFT_BY_USER, {
        variables: {
          account: user
        },
        pollInterval: 3000
    });



    useEffect(() => {

        if (data && data.tokens && user) {
            const ownedData = data.tokens.map((row) => ({
                nft: row.rarity == 1 ? NFT_COLLECTION.common : row.rarity == 2 ? NFT_COLLECTION.rare : row.rarity == 3 ? NFT_COLLECTION.superrare : NFT_COLLECTION.ultimaterare,
                status: row.offer ? "listed" : "owned",
                description: NFT_DESCRIPTION[row.rarity],
                id: parseInt(row.id),
                saleData: {
                    saleId: row.offer ? row.offer.offerId : '',
                    price: row.offer && row.offer.price ? row.offer.price : '',
                    currency: row.offer && row.offer.isShazuPrice ? 'SHAZU' : 'BNB'
                }
            }))

            setNft(ownedData)
        }

        return () => {
          mountedRef.current = false;
        };
    }, [data, mountedRef]);

    return nft
}

export default useOwnedNFT;