import { useState, useEffect } from 'react';
import { utils, BigNumber, constants} from 'ethers';

import { Container, Typography, Box, Button, Stack, useTheme, styled, Grid, DialogContent, useMediaQuery, LinearProgress } from '@mui/material';

import { useContractFunction, useEthers } from '@usedapp/core';

import { SHAZU_MARKETPLACE_CONTRACT} from '../config';

const RemoveListingButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [buttonText, setButtonText] = useState('Remove Listing');

  const { state, send } = useContractFunction(SHAZU_MARKETPLACE_CONTRACT, 'cancelOffer', { transactionName: 'Cancel Offer' });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      
      setDisabled(false)
      setButtonText('Remove Listing');
    }

    if (state.status === 'None') {
      setDisabled(false)
      setButtonText('Remove Listing');
    }

    if (state.status === 'Success') {
      setButtonText('Remove Listing');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setButtonText('Removing...');
    }
  }, [state]);

  const handleRemoveListing = () => {
    setDisabled(true)
    send(props.tokenId);
  };

  return (

    <Button color="error" onClick={handleRemoveListing}  variant="contained" disabled={disabled}> 
        {buttonText}
    </Button>
  );
};

export default RemoveListingButton;
