import { useState, useEffect } from 'react';
import { utils, BigNumber, constants} from 'ethers';

import { Container, Typography, Box, Button, Stack, useTheme, styled, Grid, DialogContent, useMediaQuery, LinearProgress } from '@mui/material';

import { useContractFunction, useEthers, useTokenBalance } from '@usedapp/core';

import { SHAZU_CONTRACT, SHAZU_ADDRESS, SHAZU_MARKETPLACE_ADDRESS, SHAZU_MARKETPLACE_CONTRACT} from '../config';


const BuyShazuButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(false)

  const [buttonText, setButtonText] = useState('BUY');

  const { state, send } = useContractFunction(SHAZU_MARKETPLACE_CONTRACT, 'fillOffer', { transactionName: 'Fill Offer' });

  const { account } = useEthers()

  const value = constants.MaxUint256

  const balance = useTokenBalance(SHAZU_ADDRESS, account)


  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      
      setDisabled(false)
      setUsingDisabled(false)
      setButtonText('BUY');
    }

    if (state.status === 'None') {

    }

    if (state.status === 'Success') {
      setButtonText('BOUGHT');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(true)
      setButtonText('BUYING..');
    }
  }, [state]);

  const handleBuy = () => {
    setDisabled(true)
    setUsingDisabled(true)
    send(props.tokenId);
  };


  useEffect(() => {
    if (!account || (account.toLowerCase() === props.seller.toLowerCase())) {

      if (!usingDisabled) {
        setDisabled(true)
      }
    } else {
      if (account && balance && balance.gte(utils.parseEther(props.price))) {
        if (!usingDisabled) {
          setDisabled(false)
        }
      } else {
        if (!usingDisabled) {
          setDisabled(true)
        }
      }
    }

  
  }, [account, props.seller, balance]);

  return (
      <Button onClick={handleBuy} 
          variant="contained" color="success" style={{ width: '100%' }} disabled={disabled}>
          {buttonText}
      </Button>
  );
};

export default BuyShazuButton;
