const RARITY_TYPE = [
    {
        id: 'all',
        label: 'All'
    },
    {
        id: 'common',
        label: 'Common'
    },
    {
        id: 'rare',
        label: 'Rare'
    },
    {
        id: 'superrare',
        label: 'Super Rare'
    },
    {
        id: 'ultimaterare',
        label: 'Ultimate Rare'
    }
]


export default function MyNFTList(){
    const listingRef = useRef();
    const handleScrollToTop = () => {
        window.scrollTo({ behavior: 'smooth', top: listingRef.current.offsetTop-150 });
    }

    const [myNftList, setMyNftList] = useState([]);
    const [filteredNftList, setFilteredNftList] = useState({});
    const { account } = useEthers();

 

    const owned = useOwnedNFT(NFT_COLLECTION, NFT_DESCRIPTION)
    const listed = useListedNFT(NFT_COLLECTION, NFT_DESCRIPTION)
    const sold = useSoldNFT(NFT_COLLECTION, NFT_DESCRIPTION)

    const [myNftTab, setmyNftTab] = useState("OWNED");


    useEffect(() => {
        if (myNftTab == 'OWNED') {

            if(owned != undefined ) {
                setMyNftList(owned)
            } else {
                setMyNftList([])
            }
        } 

        if (myNftTab == 'LISTED') {
            if(listed != undefined ) {
                setMyNftList(listed)
            } else {
                setMyNftList([])
            }
        }

        if (myNftTab == 'SOLD') {
            if(sold != undefined ) {
                setMyNftList(sold)
            } else {
                setMyNftList([])
            }
        }
    }, [owned, listed, myNftTab, sold])


    const handleChangeNftTab = (e) => {
        if(e.target.value === 'OWNED'){
            setmyNftTab("OWNED");
        }else if(e.target.value === 'LISTED'){
            setmyNftTab("LISTED");
        }else if(e.target.value === 'SOLD'){
            setmyNftTab("SOLD");
        }
    }

    const [typeFilterOpen, setTypeFilterOpen] = useState(null);
    const [typeFilter, setTypeFilter] = useState({id: 'all', label: 'All'});
    const handleTypeFilterOpen = (event) => {
        setTypeFilterOpen(event.currentTarget);
    }
    const handleTypeFilterClose = () => {
        setTypeFilterOpen(null);
    }

    const handleTypeFilter = (type) => {
        setTypeFilter({id: type.id, label: type.label});

        if(type.id === 'all'){
            setFilteredNftList(myNftList);
        }else if(type.id === 'common'){
            setFilteredNftList(myNftList.filter((item) => item.nft.rarity === 'Common' ));
        }else if(type.id === 'rare'){
            setFilteredNftList(myNftList.filter((item) => item.nft.rarity === 'Rare' ));
        }else if(type.id === 'superrare'){
            setFilteredNftList(myNftList.filter((item) => item.nft.rarity === 'Super Rare' ));
        }else if(type.id === 'ultimaterare'){
            setFilteredNftList(myNftList.filter((item) => item.nft.rarity === 'Ultimate Rare' ));
        }

        handleTypeFilterClose();
    }

    useDidMountEffect(() => {
        console.log(typeFilter);
        handleTypeFilter(typeFilter);
    }, [myNftList]);

    // PAGINATION
    const { Component: MyNFTListItemComponent, nextPage, prevPage, setPageIndex, totalPages } = usePaginator({
        PageComponent: MyNFTListItem,
        maxPerPage: 16,
        data: filteredNftList
    });

    const [paginationPage, setPaginationPage] = useState(1);
    const handlePaginationChange = (event, value) => {
        setPaginationPage(value);
        setPageIndex(value);
        handleScrollToTop();
    }    

    return(
        <>
            <motion.div ref={listingRef} variants={varFadeInUp}>
                <Grid container alignItems="stretch" direction="row" justifyContent="flex-start" style={{ marginBottom: '2rem' }}>
                    <Grid item xs={12} md={8} lg={8} xl={8}> 
                        <div className="tab-container wide-rarity-tab" style={{ width: '100%', maxWidth: '30rem' }}>
                            <Button variant={myNftTab === 'OWNED' ? 'contained' : ''} 
                                onClick={handleChangeNftTab} value="OWNED" color="purple" className="tab-button"
                            >
                                Owned ({owned ? owned.length : 0})
                            </Button>

                            <Button variant={myNftTab === 'LISTED' ? 'contained' : ''} 
                                onClick={handleChangeNftTab} value="LISTED"  color="success"  
                                style={{ marginInline: '0.4rem'}} className="tab-button"
                            >
                                Listed ({listed ? listed.length : 0})
                            </Button>

                            <Button variant={myNftTab === 'SOLD' ? 'contained' : ''} 
                                onClick={handleChangeNftTab} value="SOLD"  color="error"  
                                style={{ marginInline: '0.4rem'}} className="tab-button"
                            >
                                Sold ({sold ? sold.length : 0})
                            </Button>
                        </div>

                        <div className="small-tab-container small-rarity-tab" style={{ width: '100%', marginInline: 'auto' }}>
                            <Stack style={{ width: '100%' }}>
                                <Stack direction="row" style={{ width: '100%' }}>
                                    <Button variant={myNftTab === 'OWNED' ? 'contained' : ''} 
                                        onClick={handleChangeNftTab} value="OWNED" color="purple" className="tab-button small-rarity-button"
                                    >
                                        Owned ({DUMMY_MY_NFT.filter((item) => item.status === 'owned' ).length})
                                    </Button>
                                </Stack>

                                <Stack direction="row" style={{ width: '100%' }}>
                                    <Button variant={myNftTab === 'LISTED' ? 'contained' : ''} 
                                        onClick={handleChangeNftTab} value="LISTED"  color="success"  
                                        style={{ marginInline: '0.4rem'}} className="tab-button small-rarity-button"
                                    >
                                        Listed ({DUMMY_MY_NFT.filter((item) => item.status === 'listed' ).length})
                                    </Button>

                                    <Button variant={myNftTab === 'SOLD' ? 'contained' : ''} 
                                        onClick={handleChangeNftTab} value="SOLD"  color="error"  
                                        style={{ marginInline: '0.4rem'}} className="tab-button small-rarity-button"
                                    >
                                        Sold ({DUMMY_MY_NFT.filter((item) => item.status === 'sold' ).length})
                                    </Button>
                                </Stack>
                            </Stack>
                        </div>
                    </Grid>

                    <Grid item xs={12} md={4} lg={4} xl={4} style={{ display: 'flex' }}>
                        <Button variant="container"
                            style={{ border: '1px solid rgba(255, 255, 255, 0.3)', marginTop: 'auto', marginBottom: 'auto', marginLeft: 'auto' }} 
                            onClick={handleTypeFilterOpen}
                            className="nft-rarity-button"
                        >
                            Rarity:&nbsp;
                            <Typography component="span" variant="subtitle2" sx={{ color: 'text.secondary' }}>
                                {typeFilter.label}
                            </Typography>
                        </Button>

                        <Menu
                            keepMounted
                            anchorEl={typeFilterOpen}
                            open={Boolean(typeFilterOpen)}
                            onClose={handleTypeFilterClose}
                            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                        >
                            {RARITY_TYPE.map((option) => (
                                <MenuItem
                                    key={option.value}
                                    selected={option.id === typeFilter.id}
                                    onClick={() => handleTypeFilter(option)}
                                    sx={{ typography: 'body2' }}
                                >
                                    {option.label}
                                </MenuItem>
                            ))}
                        </Menu>
                    </Grid>
                </Grid>
            </motion.div>

            {filteredNftList.length !== 0
            ?
                <>
                    <MyNFTListItemComponent />

                    <motion.div variants={varFadeInUp}>
                        <Stack justifyContent="center" alignItems="center" style={{ width: '100%', marginTop: '2rem' }}>
                            <Pagination 
                                size="large"
                                count={totalPages} page={paginationPage} 
                                color="yellow" onChange={handlePaginationChange} 
                            />
                        </Stack>
                    </motion.div>    
                </>
            :
                <motion.div variants={varFadeInUp} style={{ width: '100%', marginTop: '4rem'}}>
                    <Stack alignItems="center" justifyContent="center" style={{ minHeight: '30rem' }}>
                        <Typography variant="h4">No NFT found</Typography>
                        <Typography variant="h6" style={{ fontWeight: '500' }}>Try another Tab or Rarity category.</Typography>
                    </Stack>
                </motion.div>
            }

            <div style={{ marginBottom: '5rem'}} />
        </>
    )
}