import { styled, useTheme  } from '@mui/material/styles';
import { useEffect, useState } from 'react';
import { utils, BigNumber, constants} from 'ethers'
import Lottie from 'react-lottie';
import { Box, Link, Typography, Stack, Popover, Button, TextField, DialogContent } from '@mui/material';
import useSound from 'use-sound';
import { shortenAddress, shortenIfAddress } from '@usedapp/core';
import { NavLink, NavLink as RouterLink } from 'react-router-dom';

import { fCRCurrency } from '../../utils/formatNumber';

// components
import { DialogAnimate, varZoomInUp } from '../animate';

import SuccessCheckLottie from '../../assets/lottie/successCheck.json';
import FailedLottie from '../../assets/lottie/failed.json';
import popDingSound from '../../assets/sfx/popDing.wav';
import { useEthers, useContractFunction, useTokenAllowance} from '@usedapp/core';
import { SHAZU_ADDRESS, SHAZU_MARKETPLACE_ADDRESS, SHAZU_NFT_CONTRACT} from '../../config'
import BuyButton from '../BuyButton';
import ApproveShazuButton from '../ApproveShazuButton';
import BuyShazuButton from '../BuyShazuButton';

const successCheckOptions = {
    loop: false,
    autoplay: true,
    animationData: SuccessCheckLottie,
    background: 'transparent'
};

const failedOptions = {
    loop: false,
    autoplay: true,
    animationData: FailedLottie,
    background: 'transparent'
};

const infoButtonStyle = { padding: '0.25rem', borderRadius: '10rem', width: '2.4rem', height: '2.4rem', minWidth: 'unset'}

const editButtonStyle = { padding: '0.4rem', borderRadius: '8px', width: '2.2rem', height: '2.2rem', minWidth: 'unset'}

const CustomTextField = styled(TextField)({
    '& label.Mui-focused': {
        color: 'white',
    },
    '& .MuiInput-underline:after': {
        borderBottomColor: 'white',
    },
    '& .MuiOutlinedInput-root': {
        '& fieldset': {
            borderColor: '#3B3B3B',
        },
        '&:hover fieldset': {
            borderColor: 'white',
        },
        '&.Mui-focused fieldset': {
            borderColor: 'white',
        },
    },
});

export default function NFTCard(props){

    const {account} = useEthers()


    const allowance = useTokenAllowance(SHAZU_ADDRESS, account, SHAZU_MARKETPLACE_ADDRESS)

    const { state, send } = useContractFunction(SHAZU_NFT_CONTRACT, 'transferFrom', {
        transactionName: 'Transfer SHAZU NFT'
    });

    const [isTransfering, setisTransfering] = useState(false)

    const [isTransfered, setIsTransfered] = useState(false)

    useEffect(() => {
        if (state.status === 'Exception') {


            setisTransfering(false)
        }
    
        if (state.status === 'Fail') {

            setisTransfering(false)
        }
    
        if (state.status === 'None') {

        }
    
        if (state.status === 'Success') {

            setisTransfering(true)

            setIsTransfered(true)
        }
    
    }, [state, account]);

    const theme = useTheme();
    const collectionCardStyle = {
        borderRadius: theme.shape.borderRadiusMd,
        backgroundColor: '#1C1C1C',
        border: 'solid 1px',
        borderColor: '#3B3B3B',
        padding: '0.5rem 0.8rem',
        transition: 'border-color 0.3s',
        maxWidth: props.big ? '25rem' : '17rem',
        marginLeft: 'auto',
        marginRight: 'auto',
        textAlign: 'left'
    }

    function getRarityColor(){
        if(props.collection.nft.rarity === 'Common'){
            return 'var(--shazu-yellow)';
        }
        if(props.collection.nft.rarity === 'Rare'){
            return 'var(--shazu-blue)';
        }
        if(props.collection.nft.rarity === 'Super Rare'){
            return 'var(--shazu-green)';
        }
        if(props.collection.nft.rarity === 'Ultimate Rare'){
            return 'linear-gradient(90deg, rgba(236,35,255,1) 0%, rgba(166,37,255,1) 34%, rgba(173,0,255,1) 100%)';
        }
    }

    function getGradientClass(){
        if(props.collection.nft.rarity === 'Common'){
            return 'gradient-border-yellow';
        }
        if(props.collection.nft.rarity === 'Rare'){
            return 'gradient-border-blue';
        }
        if(props.collection.nft.rarity === 'Super Rare'){
            return 'gradient-border-green';
        }
        if(props.collection.nft.rarity === 'Ultimate Rare'){
            return 'gradient-border-purple';
        }
    }
    const rarityLabelStyle = {
        position: 'absolute', 
        padding: '0.1rem 0.5rem', 
        borderRadius: '4px', 
        background: getRarityColor(), 
        color: props.collection.nft.rarity !== 'Ultimate Rare' ? 'black' : 'white', 
        fontSize: '0.8rem', 
        top: '-3rem', 
        left: '-0.5rem', 
        transform: 'rotate(-12deg)',
        width: 'max-content'
    }


    const [infoOpen, setInfoOpen] = useState(null);
    
    // EDIT LISTING
    const handleEditListing = (e) => {        
        const listingData = JSON.parse(e.target.value);
        props.handleOpenEditListingDialog(listingData);
    }

    // TRANSFER
    const [playPopDingSound, { stopPopDingSound }] = useSound(popDingSound);
    const [openSuccessTransferDialog, setOpenSuccessTransferDialog] = useState(false);
    const [openFailedTransferDialog, setOpenFailedTransferDialog] = useState(false);
    const [shortenedTransferAddress, setShortenedTransferAddress] = useState('');
    


    const handleOpenSuccessTransferDialog = () => {
        setOpenSuccessTransferDialog(true);
    }
    const handleCloseSuccessTransferDialog = () => {
        setOpenSuccessTransferDialog(false);
        setTransferAddress('');
    }


    const handleOpenFailedTransferDialog = () => {
        setOpenFailedTransferDialog(true);
    }
    const handleCloseFailedTransferDialog = () => {
        setOpenFailedTransferDialog(false);
    }

    const [transferAddress, setTransferAddress] = useState('');
    const [isValidAddress, setIsValidAddress] = useState(false);

    const checkTransferAddress = () => {
        try {
            shortenAddress(transferAddress);
            return true;
        } catch (error) {
            return false;
        }
    }

    useEffect(() => {
        setIsValidAddress(checkTransferAddress());
    }, [transferAddress]);

    const handleInputChange = (e) => {
        setTransferAddress(e.target.value);
    }

    const [click, setCLickTransfer] = useState(null);
    const handleClickTransfer = (event) => {
        setCLickTransfer(event.currentTarget);
    };

    const handleCloseTransfer = () => {
        setCLickTransfer(null);
    };

    const handleTransfer = () => {
        if(transferAddress === '' || transferAddress === null){
            alert('Fill the Destination Address first!');
            return false;
        }

        if (transferAddress === account) {
            alert('You cannot send token to yourself!');
            return false;
        }
        send(account, transferAddress, props.collection.id)

        setShortenedTransferAddress(shortenAddress(transferAddress));

        setisTransfering(true)
    }

    const handleListing = (e) => {
        const listingData = JSON.parse(e.target.value);
        console.info(listingData);

        props.handleOpenListingDialog(listingData);
    }

    // BUY NFT
    // STATUS DIALOG
    const [openStatusDialog, setOpenStatusDialog] = useState({ open: false, success: false });
    const [statusDialogMsg, setStatusDialogMsg] = useState('');
    const [statusDialogInfo, setStatusDialogInfo] = useState('');
    const [failedStatusMsg, setFailedStatusMsg] = useState('');

    const handleOpenSuccessDialog = (msg, info) => {
        playPopDingSound();
        setStatusDialogMsg(msg);
        setStatusDialogInfo(info);
        setOpenStatusDialog({ open: true, success: true });
    }

    const handleOpenFailedDialog = (msg, reason) => {
        playPopDingSound();
        /*setStatusDialogMsg(msg);
        setFailedStatusMsg(reason);
        setOpenStatusDialog({ open: true, success: true });
        */
        setStatusDialogMsg(msg);
        setStatusDialogInfo(reason);
        setOpenStatusDialog({ open: true, success: true });
    }

    const handleCloseStatusDialog = () => {
        setOpenStatusDialog({ ...openStatusDialog, open: false });
        setStatusDialogMsg('');
        setFailedStatusMsg('');
    }

    const handleBuy = (e) => {
        const nftData = JSON.parse(e.target.value);

        //handleOpenSuccessDialog('Buy NFT Successful', `id: ${nftData.nftId}`)
        // handleOpenFailedDialog('Buy NFT Failed', 'failed reason here');
    }

    if(props.unknown){
        return(
            <div style={{ height: '100%' }}>
                <Box className={`gradient-border p-relative ${getGradientClass()}`} style={ collectionCardStyle }>
                    <div style={{ visibility: 'hidden' }}>
                        <div style={{ width: '100%', borderRadius: theme.shape.borderRadiusMd, marginTop: '0.5rem', marginBottom: '0.5em'  }} >
                            <video className="vid" playsInline muted autoPlay loop style={{ width: '100%', borderRadius: '8px', aspectRatio: '1' }} >
                                <source src={props.collection.nft.video} type="video/mp4"/>
                            </video>
                        </div>
                        <div style={{ position: 'relative', height: '0', width: '0'}}>
                            <div style={rarityLabelStyle}>
                                {props.collection.nft.rarity}
                            </div>
                        </div>
        
                        <Typography variant="h6" style={{ fontWeight: '700', marginBottom: '0.8rem' }}>
                            {props.collection.nft.name}
                        </Typography>
        
                        {(props.amount || props.amount !== 0 || props.amount !== null) &&
                            <div className="p-relative w-100">
                                <div className="nft-card-amount">
                                    x{props.amount}
                                </div>
                            </div>
                        }
                    </div>

                    <div style={{ position: 'absolute', top: '0', left: '0', 
                        width: '100%', height: '100%', display: 'flex', 
                        flexDirection: 'column', justifyContent: 'center', 
                        alignItems: 'center', opacity: '0.2' }}
                    >
                        <Typography style={{ fontSize: '4rem'}}>
                            ?
                        </Typography>
                        <Typography>
                            Not owned yet
                        </Typography>
                    </div>
                </Box>
            </div>
        )
    }

    if(props.sold){
        return(
            <div>
                <Box className={`gradient-border ${getGradientClass()}`} style={{ ...collectionCardStyle, position: 'relative' }}>
                    <div style={{ width: '100%', borderRadius: theme.shape.borderRadiusMd, marginTop: '0.5rem', marginBottom: '0.5em'  }} >
                        <video className="vid" playsInline muted autoPlay loop style={{ width: '100%', borderRadius: '8px', aspectRatio: '1' }} >
                            <source src={props.collection.nft.video} type="video/mp4"/>
                        </video>
                    </div>
                    <div style={{ position: 'relative', height: '0', width: '0'}}>
                        <div style={rarityLabelStyle}>
                            {props.collection.nft.rarity}
                        </div>
                    </div>
    
                    <Stack direction="row" justifyContent="space-between" alignItems="center" style={{ marginBottom: '0.8rem'}} >
                        <Stack>
                            <Typography variant="h6" style={{ fontWeight: '700', marginBottom: '0.8rem' }}>
                                {props.collection.nft.name}
                            </Typography>

                            {(typeof props.collection.id !== 'undefined') &&
                                <Typography style={{ opacity: '0.5', fontSize: '0.8rem', marginTop: '-1rem' }}>
                                    id: {props.collection.id}
                                </Typography>
                            }
                        </Stack>

                        {typeof props.collection.description !== 'undefined' &&
                            <>
                                <Button variant="container" style={infoButtonStyle} onClick={(e) => setInfoOpen(e.currentTarget)}>
                                    <img src="/static/SHAZU/icons/info.svg" className="pe-none" alt="" style={{ width: '100%', opacity: '0.5' }} />
                                </Button>
                                
                                <Popover
                                    open={Boolean(infoOpen)}
                                    anchorEl={infoOpen}
                                    onClose={() => setInfoOpen(null)}
                                    anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
                                    transformOrigin={{ vertical: 'bottom', horizontal: 'center' }}
                                    PaperProps={{
                                        sx: {
                                            p: 1,
                                            maxWidth: 200
                                        }
                                    }}
                                >
                                    {props.collection.description}
                                </Popover>
                            </>
                        }   
                    </Stack>
    
                    {(typeof props.amount !== 'undefined') &&
                        <div className="p-relative w-100">
                            <div className="nft-card-amount">
                                x{props.amount}
                            </div>
                        </div>
                    }

                    <div className="sold-overlay">
                        <img src="/static/SHAZU/misc/sold-tape.png" alt="" />
                    </div>
                </Box>
            </div>
        )
    }

    return(
        <div>
            {/* TRANSFER DIALOG */}
            <DialogAnimate open={openSuccessTransferDialog} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={handleCloseSuccessTransferDialog}>
                <DialogContent style={{ textAlign: 'center' }}>
                <div className="successCheck-lottie-container">
                    <Lottie options={successCheckOptions} isClickToPauseDisabled />
                </div>
                <Typography variant="h3">
                    Transfer Successful!
                </Typography>
                
                <div className="elevate-card">
                    <Typography variant="h6">
                        Transferred to: <br/> 
                        <a href={`https://bscscan.com/address/${transferAddress}`} rel="noreferrer" target="_blank">
                            {shortenedTransferAddress}
                        </a>
                    </Typography>
                </div>
                </DialogContent>
            </DialogAnimate>

            {/* TRANSFER DIALOG FAILED */}
            <DialogAnimate open={openFailedTransferDialog} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={handleCloseFailedTransferDialog}>
                <DialogContent style={{ textAlign: 'center' }}>
                <div className="successCheck-lottie-container">
                <Lottie options={failedOptions} isClickToPauseDisabled />
                </div>
                <Typography variant="h3">
                    Transfer Failed
                </Typography>
                </DialogContent>
            </DialogAnimate>

            <Box className={`gradient-border ${getGradientClass()}`} style={ collectionCardStyle }>
                {(typeof props.collection.saleData !== 'undefined' && props.editListingAction) &&
                    <>
                        <div className="p-relative w-100">
                            <img src="/static/SHAZU/misc/sale-sticker.svg" alt="" className="swing-rotate-normal" style={{ position: 'absolute', width: '6rem', right: '-2.4rem', top: '-2.4rem' }} />
                        </div>
                    </>
                }

                <div style={{ width: '100%', borderRadius: theme.shape.borderRadiusMd, marginTop: '0.5rem', marginBottom: '0.5em'  }} >
                    <video className="vid" playsInline muted autoPlay loop style={{ width: '100%', borderRadius: '8px', aspectRatio: '1' }} >
                        <source src={props.collection.nft.video} type="video/mp4"/>
                    </video>
                </div>
                <div style={{ position: 'relative', height: '0', width: '0'}}>
                    <div style={rarityLabelStyle}>
                        {props.collection.nft.rarity}
                    </div>
                </div>

                <Stack direction="row" justifyContent="space-between" alignItems="center" style={{ marginBottom: '0.8rem'}} >
                    <Stack>
                        <Typography variant="h6" style={{ fontWeight: '700', marginBottom: '0.8rem' }}>
                            {props.collection.nft.name}
                        </Typography>

                        {(typeof props.collection.id !== 'undefined') &&
                            <Typography style={{ opacity: '0.5', fontSize: '0.8rem', marginTop: '-1rem' }}>
                                id: {props.collection.id}
                            </Typography>
                        }
                    </Stack>

                    {typeof props.collection.description !== 'undefined' &&
                        <>
                            <Button variant="container" style={infoButtonStyle} onClick={(e) => setInfoOpen(e.currentTarget)}>
                                <img src="/static/SHAZU/icons/info.svg" className="pe-none" alt="" style={{ width: '100%', opacity: '0.5' }} />
                            </Button>
                            
                            <Popover
                                open={Boolean(infoOpen)}
                                anchorEl={infoOpen}
                                onClose={() => setInfoOpen(null)}
                                anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
                                transformOrigin={{ vertical: 'bottom', horizontal: 'center' }}
                                PaperProps={{
                                    sx: {
                                        p: 1,
                                        maxWidth: 200
                                    }
                                }}
                            >
                                {props.collection.description}
                            </Popover>
                        </>
                    }   
                </Stack>

                {props.ownedAction && !isTransfered &&
                    <div className="nft-owned-action shadow-sm">
                        <Stack direction="row" spacing={2} justifyContent="space-between" alignItems="center">
                            <Stack direction="row" spacing={1} style={{ width: '100%'}}>
                                <Button onClick={handleClickTransfer} variant="contained" color="yellow" className="nft-owned-action-btn">
                                    Transfer
                                </Button>

                                <Popover
                                    open={Boolean(click)}
                                    anchorEl={click}
                                    onClose={handleCloseTransfer}
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'center'
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'center'
                                }}
                                >
                                    <Box sx={{ p: 2, maxWidth: '30rem', width: '90vw' }}>
                                        <Typography variant="subtitle1" gutterBottom>
                                            Destination Address
                                        </Typography>

                                        <CustomTextField fullWidth type="search" size="small" onChange={handleInputChange}/>

                                        <Typography variant="body2" sx={{ color: 'text.secondary', marginTop: '0.2rem' }}>
                                            {props.collection.nft.name} ({props.collection.id})
                                        </Typography>

                                        <Button disabled={!isValidAddress || isTransfering}
                                            variant="contained" color="yellow"
                                            onClick={handleTransfer} style={{ marginTop: '2rem', width: '100%' }}
                                        >
                                            {isTransfering ? "Transferring..." : "Transfer" }
                                        </Button>
                                    </Box>
                                </Popover>

                                <Button onClick={handleListing} value={JSON.stringify(props.collection)} variant="contained" color="success" className="nft-owned-action-btn">
                                    List
                                </Button>
                            </Stack>
                        </Stack>
                    </div>
                }

                {(typeof props.amount !== 'undefined') &&
                    <div className="p-relative w-100">
                        <div className="nft-card-amount">
                            x{props.amount}
                        </div>
                    </div>
                }

                {/* EDIT LISTING ACTION */}
                {(typeof props.collection.saleData !== 'undefined' && props.editListingAction) 
                    &&
                    <div className="p-relative w-100">
                        <div className="nft-sale-info shadow-sm">
                            <Stack direction="row" spacing={2} justifyContent="space-between" alignItems="center">
                                <Stack>
                                    <div className="nft-sale-caption">
                                        Listing price
                                    </div>
                                    <Stack direction="row" alignItems="center" spacing={1}>
                                        <div>
                                        {utils.parseEther(props.collection.saleData.price).gt(utils.parseEther("1")) ? fCRCurrency(props.collection.saleData.price) : props.collection.saleData.price} {props.collection.saleData.currency}
                                        </div>
                                        {
                                            props.collection.saleData.currency === 'BNB' &&
                                            <img src="/static/SHAZU/icons/ic_bnb.svg" alt="" style={{ width: '1rem', height: '1rem', marginLeft: '0.2rem', marginBottom: '0.2rem' }}/>
                                        }
                                        {
                                            props.collection.saleData.currency === 'SHAZU' &&
                                            <img src="/static/SHAZU/icons/ic_shazu.png" alt="" style={{ width: '1rem', height: '1rem', marginLeft: '0.2rem', marginBottom: '0.2rem' }} />
                                        }
                                    </Stack>
                                </Stack>

                                <Button variant="container" value={JSON.stringify(props.collection)} style={editButtonStyle} onClick={handleEditListing}>
                                    <img src="/static/SHAZU/icons/edit.svg" className="pe-none" alt="" style={{ width: '100%', opacity: '0.5' }} />
                                </Button>
                            </Stack>
                        </div>
                    </div>
                }

                {/* MARKETPLACE LISTING ACTION */}
                {(typeof props.collection.marketData !== 'undefined' && props.marketplaceListing)
                    &&
                    <>
                        <div className="elevate-card-sm" style={{ marginBottom: '1rem'}}>
                            <Stack direction="column" justifyContent="space-between" >
                                <div style={{ fontSize: '0.8rem'}}>
                                    Price: 
                                </div>

                                <Stack direction="row" alignItems="center" spacing={1}>
                                    <div style={{ fontSize: '1rem', fontWeight: '700'}}>
                                        {utils.parseEther(props.collection.saleData.price).gt(utils.parseEther("1")) ? fCRCurrency(props.collection.saleData.price) : props.collection.saleData.price} {props.collection.saleData.currency}
                                    </div>
                                    {
                                        props.collection.saleData.currency === 'BNB' &&
                                        <img src="/static/SHAZU/icons/ic_bnb.svg" alt="" style={{ width: '1.2rem', height: '1.2rem', marginLeft: '0.2rem', marginBottom: '0.1rem' }}/>
                                    }
                                    {
                                        props.collection.saleData.currency === 'SHAZU' &&
                                        <img src="/static/SHAZU/icons/ic_shazu.png" alt="" style={{ width: '1.2rem', height: '1.2rem', marginLeft: '0.2rem', marginBottom: '0.1rem' }} />
                                    }
                                </Stack>
                            </Stack>
                        </div>
                      
                        <Stack direction="row" spacing={1}>
                            
                            {(props.collection.saleData.currency == 'SHAZU') ? (
                                (allowance && allowance.gte(utils.parseEther(props.collection.saleData.price))) ? (
                                    <BuyShazuButton tokenId = {props.collection.saleData.saleId} price={props.collection.saleData.price} seller={props.collection.saleData.seller}/>
                                ):(
                                    <ApproveShazuButton price={props.collection.saleData.price} seller={props.collection.saleData.seller} />
                                )
                            ): (
                                <BuyButton tokenId = {props.collection.saleData.saleId} price={props.collection.saleData.price} seller={props.collection.saleData.seller}/>
                            )}
                        </Stack>

                        {/* STATUS DIALOG */}
                        <DialogAnimate open={openStatusDialog.open} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={handleCloseStatusDialog}>
                            <DialogContent style={{ textAlign: 'center' }}>
                                {
                                    openStatusDialog.success 
                                    ?
                                    <div className="successCheck-lottie-container">
                                        <Lottie options={successCheckOptions} isClickToPauseDisabled />
                                    </div>
                                    :
                                    <div className="successCheck-lottie-container">
                                        <Lottie options={failedOptions} isClickToPauseDisabled />
                                    </div>
                                }
                                
                                <Typography variant="h3">
                                    {statusDialogMsg}
                                </Typography>
                                
                                {
                                    openStatusDialog.success 
                                    ?
                                    <div className="elevate-card">
                                        <Typography variant="h5">
                                            {statusDialogInfo}
                                        </Typography>
                                    </div>
                                    :
                                    <div>
                                        {failedStatusMsg}
                                    </div>
                                }
                            </DialogContent>
                        </DialogAnimate>
                    </>
                }

                {/* TOP SALE */}
                {(typeof props.collection.topSaleData !== 'undefined' && props.topSale)
                    &&
                    <>
                        <div className="elevate-card" style={{ marginBottom: '0.4rem', marginTop: '-0.4rem' }}>
                            <Stack alignItems="center">
                                <div style={{ fontSize: '1.2rem', fontWeight: '700'}}>
                                {utils.parseEther(props.collection.topSaleData.price).gt(utils.parseEther("1")) ? fCRCurrency(props.collection.topSaleData.price) : props.collection.topSaleData.price} {props.collection.topSaleData.currency}
                                </div>
                            </Stack>
                        </div>
                    </>
                }
            </Box>
        </div>
    )

}
