import { gql } from '@apollo/client';


export const GET_NFT_BY_USER = gql`
  query GetNFT($account: String!) {
    tokens(first: 1000, orderBy:id, orderDirection:desc, where: { owner: $account }) {
        rarity
        id
        offer {
          id
          offerId
          price
          isShazuPrice
          cancelled
          fulfilled
        }
    }
  }
`;

export const GET_LISTED_NFT_BY_USER = gql`
  query GetNFT($account: String!) {
    tokens(first: 1000, orderBy:id, orderDirection:desc, where: { owner: $account,  offer_not: null}) {
        rarity
        id
        offer {
          id
          offerId
          price
          isShazuPrice
          cancelled
          fulfilled
        }
    }
  }
`;

export const GET_SOLD_NFT_BY_USER = gql`
query GetNFT($account: String!) {
    offers(first: 1000, orderBy:id, orderDirection:desc, where:{user: $account, fulfilled: true}) {
            id
            offerId
            token {
            id
        }
        rarity
        price
        isShazuPrice
        cancelled
        fulfilled
    }
  }
`;


export const GET_BURNED_NFT_BY_USER = gql`
query GetNFT($account: String!) {
    transfers(first: 1000, orderBy:id, orderDirection:desc, where:{from: $account, to: "0x0000000000000000000000000000000000000000"}) {
      id
      token {
        id
        rarity
      }
      
      from {
        id
      }
    }
  }
`;

export const LAST_MINT_NFT = gql`
query GetScratches {
  scratches(first: 1000, orderBy:id, orderDirection:desc) {
    owner {
      id
    }
    
    rarity
    
    token {
      id
    }
    
    rarity
    block
    transactionHash
    timestamp
  }
}
`

export const LAST_TOP_SALE = gql`
query GetNFT {
  offers(first: 4, orderBy:price, orderDirection:desc, where:{fulfilled: true}) {
      id
      offerId
      token {
          id
      }
      buyer {
        id
      }
      rarity
      price
      isShazuPrice
      cancelled
      fulfilled
  }
}
`


export const SALE_HISTORY = gql`
query GetNFT {
  offers(first: 1000, orderBy:block, orderDirection:desc, where:{fulfilled: true}) {
      id
      offerId
      token {
          id
      }
      buyer {
        id
      }

      user {
        id
      }

      rarity
      price
      isShazuPrice
      cancelled
      fulfilled
      timestamp
  }
}
`

export const MARKETPLACE_HISTORY = gql`
query GetNFT {
  offers(first: 1000, orderBy:price, orderDirection:asc, where:{fulfilled: false, cancelled:false}) {
      id
      offerId
      token {
          id
      }
      user {
        id
      }
      rarity
      price
      isShazuPrice
      cancelled
      fulfilled
      timestamp
  }
}
`

export const STAT = gql`
query GetStat {
  stats {
    minSalePriceBNB
    minSalePriceSHAZU
    volumeBNB
    volumeSHAZU
  }
}
`