import { useEffect, useState, useRef } from 'react';
import { useBlockNumber, useEthers } from '@usedapp/core';
import { useQuery } from '@apollo/client';
import { GET_SOLD_NFT_BY_USER} from '../queries';


const useSoldNFT = (NFT_COLLECTION, NFT_DESCRIPTION) => {

    const mountedRef = useRef(true);

    const [nft, setNft] = useState(undefined);

    const { account } = useEthers();

    const [user, setUser] = useState(undefined);

    useEffect(() => {
        if (account) {
          setUser(account.toLowerCase());
        } else {
          setUser(undefined);
        }
      }, [account]);

    const { data } = useQuery(GET_SOLD_NFT_BY_USER, {
        variables: {
          account: user
        },
        pollInterval: 3000
    });

    useEffect(() => {
        if (data && data.offers && user) {
            const ownedData = data.offers.map((row) => ({
                nft: row.rarity == 1 ? NFT_COLLECTION.common : row.rarity == 2 ? NFT_COLLECTION.rare : row.rarity == 3 ? NFT_COLLECTION.superrare : NFT_COLLECTION.ultimaterare,
                status: 'sold',
                description: NFT_DESCRIPTION[row.rarity],
                id: parseInt(row.token.id),
                saleData: {
                    price: row.price ? row.price : '',
                    currency: row.isShazuPrice ? 'SHAZU' : 'BNB'
                }
            }))

            setNft(ownedData)
        }

        return () => {
          mountedRef.current = false;
        };
    }, [data, mountedRef]);

    return nft
}

export default useSoldNFT;